html {
  font-size: 62.5%;
}

body {
  font-size: 1.6rem;
}

/* Chrome, Safari, Edge, Opera */

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */

input[type="number"] {
  -moz-appearance: textfield;
}

//Buttons//
.cl-btn {
  min-width: 15rem;
}

.cl-btn-small {
  min-width: 10rem;
  border-radius: 0.5rem !important;
}

.cl-btn-primary {
  @include cl-btn(
    $cl-primary,
    $cl-primary-darker,
    $cl-primary-darker,
    $cl-primary
  );
  &:hover {
    box-shadow: none;
  }
}

.cl-btn-secondary {
  @include cl-btn(
    $cl-secondary,
    $cl-secondary-dark,
    $cl-secondary-dark,
    $cl-secondary
  );
}

.cl-btn-tartiary {
  @include cl-btn(
    $cl-primary-dark,
    $cl-primary-darker,
    $cl-primary-darker,
    $cl-primary-dark
  );
  &:hover {
    box-shadow: none;
  }
}

.cl-btn-supporting-1 {
  @include cl-btn(
    $cl-supporting1,
    $cl-supporting1-dark,
    $cl-supporting1-dark,
    $cl-supporting1
  );
}

.cl-btn-supporting-2 {
  @include cl-btn(
    $cl-supporting2,
    $cl-supporting2-dark,
    $cl-supporting2-dark,
    $cl-supporting2
  );
}

.cl-btn-gray {
  @include cl-btn($cl-gray, $cl-gray-dark, $cl-gray-dark, $cl-gray);
}

.cl-btn-outline {
  @include cl-btn(transparent, $cl-gray-dark, $cl-gray-dark, $cl-gray);
  color: $cl-gray;
}

.cl-white {
  color: $cl-white;
}

.cl-red {
