  button {
    font-size: 1.6rem;
    cursor: pointer;
    background-color: transparent;
    outline: none;
    border: transparent;
    color: $cl-white;
    padding: 0.5rem 1.5rem;
    .user-icon {
      font-size: 2rem;
    }
  }
  button.dropdown-item {
    font-size: 1.4rem;
    &:hover {
      background-color: $cl-dropdown-hover-bg;
      color: $cl-primary-darkest;
    }
  }
  .btn-secondary:focus {
    box-shadow: none !important;
    background: none !important;
  }
}

.input-group {
  background: $cl-primary-dark;
  input.form-control {
    outline: none;
    border-radius: 0;
    font-size: 1.6rem;
